// in app/ledger/page.tsx
'use client'

import { createClient } from '@/utils/supabase/client'
import { useState, useEffect } from 'react'
import { User } from '@supabase/supabase-js'

type AuditLogEvent = {
  event_date: string
  event_type: 'demerit' | 'served'
  title: string
  details: string | null
  amount: number
  actor_name: string
  status: string
}

export default function LedgerPage() {
  const supabase = createClient()
  const [log, setLog] = useState<AuditLogEvent[]>([])
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    async function getLog() {
      setLoading(true)
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        setLoading(false)
        setError("You must be logged in to view this page.")
        return
      }
      setUser(user)

      const { data, error } = await supabase
        .rpc('get_cadet_audit_log', { p_cadet_id: user.id })
        
      if (error) {
        setError(error.message)
      } else {
        setLog(data as AuditLogEvent[])
      }
      setLoading(false)
    }
    getLog()
  }, [supabase])

  const formatStatus = (status: string) => {
    switch (status) {
      case 'completed': return 'Approved'
      case 'rejected': return 'Rejected'
      case 'needs_revision': return 'Awaiting Revision'
      case 'pending_approval': return 'Pending Approval'
      default: return status
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed': return 'bg-green-100 text-green-800'
      case 'rejected': return 'bg-red-100 text-red-800'
      case 'pending_approval': return 'bg-yellow-100 text-yellow-800'
      case 'needs_revision': return 'bg-blue-100 text-blue-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="max-w-4xl mx-auto p-4 sm:p-6 lg:p-8">
      <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
        My Audit Log
      </h1>
      <p className="mt-2 text-lg text-gray-600 dark:text-gray-400">
        A complete history of all demerits and tours.
      </p>

      {loading && <p className="mt-6 text-gray-500">Loading log...</p>}
      {error && <p className="mt-6 text-red-600">{error}</p>}

      {!loading && !error && (
        <div className="mt-6 flow-root">
          <ul role="list" className="-mb-8">
            {log.map((event, eventIdx) => (
              <li key={eventIdx}>
                <div className="relative pb-8">
                  {eventIdx !== log.length - 1 ? (
                    <span className="absolute left-5 top-5 -ml-px h-full w-0.5 bg-gray-300 dark:bg-gray-700" aria-hidden="true" />
                  ) : null}
                  <div className="relative flex items-start space-x-3">
                    <div>
                      <span className={`h-10 w-10 rounded-full flex items-center justify-center ring-8 ring-white dark:ring-gray-900 ${
                        event.event_type === 'demerit' ? 'bg-yellow-500' : 'bg-green-500'
                      }`}>
                        {/* Icon placeholder */}
                        <span className="text-white font-bold">{event.event_type === 'demerit' ? 'D' : 'T'}</span>
                      </span>
                    </div>
                    <div className="min-w-0 flex-1 bg-white dark:bg-gray-800 p-4 rounded-lg shadow-sm border dark:border-gray-700">
                      <div className="flex justify-between items-center">
                        <p className="text-lg font-medium text-gray-900 dark:text-white">
                          {event.title}
                        </p>
                        <span className={`text-xs font-medium px-2 py-0.5 rounded-full ${
                          event.event_type === 'served' ? getStatusColor('completed') : getStatusColor(event.status)
                        }`}>
                          {formatStatus(event.status)}
                        </span>
                      </div>
                      <p className="text-sm text-gray-500 dark:text-gray-400">
                        {new Date(event.event_date).toLocaleString()}
                      </p>
                      
                      <div className="mt-2 space-y-2">
                        <p className="text-sm text-gray-700 dark:text-gray-300">
                          <strong>Amount:</strong> {event.amount > 0 ? `+${event.amount} demerits` : `${event.amount} tours`}
                        </p>
                        <p className="text-sm text-gray-700 dark:text-gray-300">
                          <strong>{event.event_type === 'demerit' ? 'Submitter:' : 'Logged by:'}</strong> {event.actor_name || 'System'}
                        </p>
                        {event.details && (
                           <p className="text-sm text-gray-700 dark:text-gray-300 italic bg-gray-50 dark:bg-gray-700 p-2 rounded">
                            "{event.details}"
                           </p>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      )}
    </div>
  )
}